@import url('https://fonts.googleapis.com/css2?family=BhuTuka+Expanded+One&display=swap');
@import url("https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'BhuTuka Expanded One', cursive;
}

body {
  background-color: black;
}

/* App */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
}

/* Counter */
.counter-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 100px;
}

h1 {
  color: #fff;
  font-size: 8em;
  font-family: "Poppins", sans-serif;
  position: absolute;
  right: 175px;
  transform: translate(-50%, -50%);
}

h1:nth-child(1) {
  color: transparent;
  -webkit-text-stroke: 1px #a6c1ee;
}

h1:nth-child(2) {
  color: #a6c1ee;
  animation: animate 4s ease-in-out infinite;
}

@keyframes animate {
  0%,
  100% {
    clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
  }

  50% {
    clip-path: polygon(
      0% 60%,
      15% 65%,
      34% 66%,
      51% 62%,
      67% 50%,
      84% 45%,
      100% 46%,
      100% 100%,
      0% 100%
    );
  }
}

.count--display {
  color: #a6c1ee;
  font-size: 7rem;
  padding-top: 100px;
}

/* Buttons */
.buttons {
  display: flex;
  gap: 100px;
  margin-top: 100px;
}

.btn {
  flex: 1 1 auto;
  margin: 10px;
  padding: 10px 30px ;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  text-shadow: 0px 0px 10px rgba(0,0,0,0.2);
  border: none;
  border-radius: 20px;
  font-size: 3rem;
  cursor: pointer;
}

.btn:hover {
  background-position: right center; 
}

.minus--btn {
  background-image: linear-gradient(to right, #fbc2eb 0%, #a6c1ee 51%, #fbc2eb 100%);
}

.plus--btn {
  background-image: linear-gradient(to right, #a1c4fd 0%, #c2e9fb 51%, #a1c4fd 100%);
}